@use "../global/mixins.scss";
@use "../global/variables.scss";

.button {
  width: 100%;
  padding: 16px 0;

  @include mixins.font;
  color: variables.$white;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  background: variables.$blue-01;
  border: none;
  cursor: pointer;

  &:hover {
    background: variables.$blue-02;
  }

  &:focus {
    background: variables.$blue-02;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
    background: variables.$blue-03;
  }

  @media (min-width: variables.$tablet-width) {
    max-width: 458px;
  }

  &--link {
    max-width: 458px;
    margin-top: 33px;
    display: block;
    text-decoration: none;

    @media (min-width: variables.$tablet-width) {
      margin-top: 42px;
    }

    @media (min-width: variables.$desktop-width) {
      margin-top: 46px;
    }
  }

  &--actions {
    @media (min-width: variables.$tablet-width) {
      width: 324px;
    }
  }

  &--error {
    margin-top: 28px;
    max-width: 180px;
  }
}
