@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  width: 100%;
  padding: 16px 0;
  font-family: "PT Sans", "Arial", sans-serif;
  color: white;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  background: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background: #669ec0;
}
.button:focus {
  background: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background: #5496bd;
}
@media (min-width: 768px) {
  .button {
    max-width: 458px;
  }
}
.button--link {
  max-width: 458px;
  margin-top: 33px;
  display: block;
  text-decoration: none;
}
@media (min-width: 768px) {
  .button--link {
    margin-top: 42px;
  }
}
@media (min-width: 1280px) {
  .button--link {
    margin-top: 46px;
  }
}
@media (min-width: 768px) {
  .button--actions {
    width: 324px;
  }
}
.button--error {
  margin-top: 28px;
  max-width: 180px;
}

.page__body {
  min-width: 320px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #f2f2f2;
  font-family: "PT Sans", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .page__body {
    max-width: 1200px;
    padding: 0;
  }
}
@media (min-width: 1280px) {
  .page__body {
    padding: 0 40px;
  }
}

.page__main {
  flex: 1 0 auto;
}

.page__footer {
  flex: 0 0 auto;
}

.main-header {
  position: relative;
}
.main-header--nojs .main-header__logo-wrapper {
  height: 0;
}
.main-header--nojs .main-header__nav {
  position: relative;
}
.main-header--nojs .main-header__nav--closed {
  display: block;
}
.main-header--nojs .main-header__toggle {
  display: none;
}
.main-header--nojs .intro__wrapper {
  margin-top: 0;
}
.main-header--nojs .second-intro__wrapper-image {
  padding: 50px 18px 63px;
}
@media (min-width: 768px) {
  .main-header--nojs .main-header__nav {
    position: fixed;
  }
  .main-header--nojs .intro__wrapper {
    margin-top: 56px;
  }
}

.main-header__logo-wrapper {
  position: absolute;
  width: 100%;
  height: 56px;
  top: 0;
  background: white;
  z-index: 9;
}
@media (min-width: 768px) {
  .main-header__logo-wrapper {
    height: 0;
    z-index: 15;
  }
}

.main-header__nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
.main-header__nav--closed {
  display: none;
}
.main-header__nav--opened {
  display: block;
}
@media (min-width: 768px) {
  .main-header__nav {
    position: fixed;
    display: block;
  }
}

.main-header__logo-image {
  position: absolute;
  top: 0;
  right: 50%;
  transform: translateX(50%);
  z-index: 4;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}
@media (min-width: 768px) {
  .main-header__logo-image {
    position: fixed;
    z-index: 6;
  }
}

.main-header__toggle {
  width: 63px;
  height: 59px;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99;
  border: none;
  background: transparent;
  cursor: pointer;
}
.main-header__toggle:hover svg {
  opacity: 0.7;
}
.main-header__toggle:active svg {
  opacity: 0.4;
}
@media (min-width: 768px) {
  .main-header__toggle {
    position: fixed;
    display: none;
  }
}

.main-header__icon-btn {
  fill: #81b3d2;
}
.main-header__icon-btn--close {
  fill: white;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background: white;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
  }
}

.site-list__item {
  position: relative;
  width: 100%;
  text-align: center;
  background: #81b3d2;
  border-top: 1px solid #b3b3b3;
  border-bottom: 1px solid #5496bd;
}
.site-list__item:last-child {
  border-bottom: none;
}
.site-list__item:last-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .site-list__item {
    background: none;
    border: none;
  }
  .site-list__item:last-child::after {
    display: none;
  }
  .site-list__item:nth-child(2) {
    margin-right: 153px;
  }
}
@media (min-width: 1280px) {
  .site-list__item:nth-child(2) {
    margin-right: 240px;
  }
}

.site-list__link {
  display: block;
  padding: 16px 0 12px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
}
.site-list__link--active {
  user-select: none;
}
@media (min-width: 768px) {
  .site-list__link {
    padding: 18px 0 17px;
    font-size: 14px;
    line-height: 21px;
    color: #333333;
  }
  .site-list__link:hover {
    color: #81b3d2;
  }
  .site-list__link:focus {
    color: #81b3d2;
  }
  .site-list__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
  .site-list__link--active {
    color: #766357;
    font-weight: 700;
  }
  .site-list__link--active:hover {
    color: #766357;
  }
}

.intro {
  position: relative;
  background: white;
}
@media (min-width: 768px) {
  .intro::before, .intro::after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    bottom: 28px;
    border: 0 solid transparent;
    border-bottom: 57px solid white;
    z-index: 2;
  }
  .intro::before {
    left: 50%;
    transform: translate(-100%, 50%);
    border-left-width: 0;
    border-right-width: 350px;
  }
  .intro::after {
    right: 50%;
    transform: translate(100%, 50%);
    border-right-width: 0;
    border-left-width: 350px;
  }
}
@media (min-width: 1280px) {
  .intro::before, .intro::after {
    bottom: 34px;
  }
}

.intro__wrapper {
  margin-top: 55px;
  background-color: #81b3d2;
  background-image: url("../img/intro_bg-mobile.jpg");
  background-repeat: no-repeat;
  background-position: 100% 100%;
  background-size: cover;
  clip-path: polygon(0 0, 100% 0, 100% 91%, 50% 98%, 0 91%);
}
@media (min-resolution: 2dppx) {
  .intro__wrapper {
    background-image: url("../img/intro_bg-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .intro__wrapper {
    margin-top: 56px;
    background-image: url("../img/intro_bg-tablet.jpg");
    clip-path: polygon(0 0, 100% 0, 100% 93%, 50% 100%, 0 93%);
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .intro__wrapper {
    background-image: url("../img/intro_bg-tablet@2x.jpg");
  }
}
@media (min-width: 1280px) {
  .intro__wrapper {
    background-image: url("../img/intro_bg-desktop.jpg");
    clip-path: polygon(0 0, 100% 0, 100% 88%, 50% 99%, 0 88%);
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .intro__wrapper {
    background-image: url("../img/intro_bg-desktop@2x.jpg");
  }
}

.intro__wrapper-image {
  padding: 90px 0;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .intro__wrapper-image {
    padding: 77px 0 86px;
  }
}
@media (min-width: 1280px) {
  .intro__wrapper-image {
    padding: 78px 0 90px;
  }
}

.intro__icon-text {
  width: 282px;
  height: 214px;
}
@media (min-width: 768px) {
  .intro__icon-text {
    width: 461px;
    height: 350px;
  }
}
@media (min-width: 1280px) {
  .intro__icon-text {
    width: 460px;
    height: 350px;
  }
}

.promo {
  background: white;
}

.promo__wrapper {
  padding: 25px 24px 31px;
  text-align: center;
}
@media (min-width: 768px) {
  .promo__wrapper {
    padding: 49px 75px 56px;
  }
}
@media (min-width: 1280px) {
  .promo__wrapper {
    padding: 54px 75px 46px;
  }
}

.promo__title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__title {
    max-width: 500px;
    margin: 0 auto;
    font-size: 21px;
    line-height: 26px;
  }
}

.promo__subtitle {
  margin: 0;
  margin-top: 35px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
}
@media (min-width: 768px) {
  .promo__subtitle {
    margin-top: 28px;
  }
}

.features {
  text-align: center;
}

.features__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(256px, auto);
  }
}

@media (min-width: 768px) {
  .features__item-list {
    grid-column: 1/4;
  }
}

.features__item {
  padding: 24px 18px 24px;
  background: #eeeeee;
  border-bottom: 1px solid #d5d5d5;
}
.features__item--accent {
  padding: 0;
  border: none;
  background: #81b3d2;
}
@media (min-width: 768px) {
  .features__item {
    border-bottom: none;
    padding: 52px 0;
  }
  .features__item--accent {
    grid-column: 1/4;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
  }
  .features__item:nth-child(3n+4) {
    padding: 42px 0;
  }
  .features__item:nth-child(3n+4) > p {
    padding: 0 40px;
  }
}
@media (min-width: 1280px) {
  .features__item {
    padding: 64px 0 40px;
  }
  .features__item--accent {
    padding: 0;
  }
  .features__item:nth-child(3n+4) {
    padding: 53px 0 40px;
  }
}

.features__item-wrapper {
  padding: 54px 18px 40px;
  color: white;
}
.features__item-wrapper--accent-first {
  position: relative;
}
.features__item-wrapper--accent-first::before {
  content: "";
  position: absolute;
  top: -3px;
  left: 0;
  background: white;
  width: 100%;
  height: 25px;
  clip-path: polygon(50% 100%, -100px 0%, 130% 0%);
}
.features__item-wrapper--accent-second {
  padding: 37px 18px 36px;
}
@media (min-width: 768px) {
  .features__item-wrapper {
    padding: 53px 20px 20px;
  }
  .features__item-wrapper--accent-first::before {
    display: none;
  }
  .features__item-wrapper--accent-second {
    order: 2;
  }
}

.features__title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .features__title {
    width: 150px;
    margin: 0 auto;
    font-size: 21px;
    line-height: 26px;
  }
  .features__title span {
    display: block;
  }
}
@media (min-width: 1280px) {
  .features__title {
    padding: 0 100px;
  }
}

.features__subject {
  display: inline-block;
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin-top: 12px;
  color: #333333;
}
.features__subject::before, .features__subject::after {
  content: "";
  width: 8px;
  height: 1px;
  background: #333333;
  position: absolute;
  top: 60%;
  transform: translateY(-55%);
}
.features__subject::before {
  left: -12px;
}
.features__subject::after {
  right: -12px;
}
.features__subject--white {
  color: white;
}
.features__subject--white::before, .features__subject--white::after {
  background: white;
}

.features__text {
  margin: 0;
  margin-top: 17px;
  padding: 0 15px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
}
.features__text--white {
  padding: 0;
  color: white;
}
@media (min-width: 768px) {
  .features__text {
    margin-top: 24px;
  }
  .features__text--white {
    padding: 0 10px;
  }
}
@media (min-width: 1280px) {
  .features__text {
    padding: 0 35px;
  }
  .features__text--white {
    padding: 0 45px;
  }
  .features__text--width {
    padding: 0 70px;
  }
}

.features__img-wrapper {
  height: 260px;
  background: #d5d5d5;
  line-height: 0;
  text-align: center;
  overflow: hidden;
}
@media (min-width: 768px) {
  .features__img-wrapper {
    height: 256px;
  }
  .features__img-wrapper--right-img {
    grid-column: 2/4;
  }
  .features__img-wrapper--left-img {
    grid-column: 3/1;
  }
}

.features__img {
  width: 100%;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}

.city-feature {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
  padding-bottom: 46px;
  background: white;
}
@media (min-width: 768px) {
  .city-feature {
    padding: 0;
    display: flex;
    align-items: baseline;
    justify-content: space-around;
    flex-wrap: wrap;
    box-sizing: border-box;
  }
}
@media (min-width: 1280px) {
  .city-feature {
    padding: 0;
  }
}

.city-feature__item {
  max-width: 458px;
  margin: 0 auto;
  padding: 32px 18px 0 65px;
  text-align: left;
}
@media (min-width: 768px) {
  .city-feature__item {
    width: 256px;
    margin: 0;
    padding: 155px 20px 62px;
    text-align: center;
    box-sizing: border-box;
  }
  .city-feature__item:nth-child(odd) {
    padding: 155px 20px 62px;
  }
  .city-feature__item:nth-child(odd) p {
    padding: 0 20px;
  }
}
@media (min-width: 1280px) {
  .city-feature__item {
    min-width: 350px;
    padding: 168px 20px 70px;
  }
  .city-feature__item:nth-child(odd) {
    padding: 168px 20px 62px;
  }
  .city-feature__item p {
    padding: 0;
  }
}

.city-feature__title {
  position: relative;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
}
.city-feature__title::before {
  content: "";
  position: absolute;
  left: -47px;
  top: 47%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
}
.city-feature__title--house::before {
  background: url("../img/icon-house.svg") center center/cover no-repeat;
}
.city-feature__title--food::before {
  background: url("../img/icon-food.svg") center center/cover no-repeat;
}
.city-feature__title--gift {
  margin-top: 1px;
}
.city-feature__title--gift::before {
  background: url("../img/icon-gift.svg") center center/cover no-repeat;
}
@media (min-width: 768px) {
  .city-feature__title {
    font-size: 21px;
    line-height: 26px;
  }
  .city-feature__title::before {
    top: -96px;
    left: 50%;
    transform: translateX(-50%);
  }
  .city-feature__title--house::before {
    width: 77px;
    height: 72px;
  }
  .city-feature__title--food::before {
    width: 80px;
    height: 70px;
  }
  .city-feature__title--gift {
    margin-top: 0;
  }
  .city-feature__title--gift::before {
    width: 71px;
    height: 81px;
  }
}
@media (min-width: 1280px) {
  .city-feature__title::before {
    left: 50%;
    transform: translateX(-50%);
  }
  .city-feature__title--house::before {
    top: -99px;
  }
  .city-feature__title--food::before {
    top: -96px;
    left: 49%;
    width: 75px;
  }
  .city-feature__title--gift::before {
    top: -102px;
  }
}

.city-feature__desc {
  display: block;
