@media (min-resolution: 2dppx) {
  .second-intro__wrapper {
    background-image: url("../img/second_intro_bg-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .second-intro__wrapper {
    background-image: url("../img/second_intro_bg-tablet.jpg");
    clip-path: polygon(0 0, 100% 0, 100% 89%, 50% 100%, 0 89%);
