.form__input[type=radio],
.form__input[type=checkbox] {
  position: absolute;
  appearance: none;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.form__input[type=radio]:checked + .form__custom-input--radio::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 16px;
  background: #333333;
  border-radius: 50%;
}

.form__input[type=checkbox]:checked + .form__custom-input--checkbox {
  background-image: url("../img/checkbox-tick.svg");
  background-color: #f2f2f2;
  background-repeat: no-repeat;
  background-position: center center;
}

.form__input[type=radio]:hover + .form__custom-input--radio,
.form__input[type=checkbox]:hover + .form__custom-input--checkbox {
  background-color: #e5e5e5;
}

.form__input[type=radio]:focus + .form__custom-input--radio,
.form__input[type=checkbox]:focus + .form__custom-input--checkbox {
  background-color: #e5e5e5;
}

.form__input[type=radio]:active + .form__custom-input--radio,
.form__input[type=checkbox]:active + .form__custom-input--checkbox {
  background-color: #d5d5d5;
}

.form__input[type=radio]:checked:active + .form__custom-input--radio,
.form__input[type=checkbox]:checked:active + .form__custom-input--checkbox {
  opacity: 0.7;
  background-color: #d5d5d5;
}

.form__input[type=radio]:checked:focus + .form__custom-input--radio,
.form__input[type=checkbox]:checked:focus + .form__custom-input--checkbox {
  background-color: #e5e5e5;
}

.form__input[type=radio]:disabled + .form__custom-input--radio,
.form__input[type=checkbox]:disabled + .form__custom-input--checkbox {
  background-color: #f2f2f2;
}

.form__input[type=radio]:disabled ~ span,
.form__input[type=checkbox]:disabled ~ span {
  color: #d5d5d5;
}

